import React, { useEffect } from 'react';
import './App.scss';
import { connect } from 'react-redux';
import JoinMeeting from "./JoinMeeting";
import Home from './Home';
import PWAInstallPopup from '../components/PWAInstallPopup/PWAInstallPopup';

import {
  MeetingViews
} from '../store/reducer';
import { 
  getUserInfo
} from "../store/thunk-actions";

function App(props) {
    let {
      meetingCurrentView,
      userInfo,
      dispatch
    } = props;

    useEffect( () => {
      console.log(userInfo);
      if(!userInfo) {
        dispatch( getUserInfo())
      }
    }, [dispatch]);
    
    return (
        <>
            <div className="app">
              { meetingCurrentView === MeetingViews.Join && <JoinMeeting /> }
              { meetingCurrentView === MeetingViews.Home && <Home /> }
            </div>
            <PWAInstallPopup />
        </>
    );
}

const mapStateToProps = ({meetingCurrentView, userInfo}) => {
  return {
    meetingCurrentView,
    userInfo
  }
}

export default connect(
  mapStateToProps,
)(App);

