import { setIsDuringMeeting } from 'store/actions.js';
import { isInStandaloneMode } from "utils/index.js";
import { getJoinMeetingPath, getStartMeetingPath, getSigninPath, getSignOutPath } from "constants/meeting-url.js";
import {setUserInfo} from 'store/actions';

export default class MeetingAgent {
    constructor({store = null}) {
        this.store = store;
        this.timerHandler = null;
        this.timerInterval = 5000; // 5 seconds;
        this.openedBrowsingContext = null; // start/join meeting will open another window/tab, we need to communicate to it
        this.hasReceiveStartMsg = false;
        this.windowName = "WebCliet Meeting";
        this.windowFeatures = "top=20,left=20,titlebar=0,toolbar=0,location=0,status=0,menubar=0";
    }
    
    reload(url) {
      if(!url) {
        window.location.reload();
      } else {
        window.location.href = url;
      }
    }
    
    openTab(url) {
      return window.open(url, "_blank");
    }
    
    openWindow(url, name, feathers) {
      return window.open(url, name = this.windowName, feathers = this.windowFeatures);
    }
    
    signIn() {
      const url = getSigninPath();
      this.reload( url );
    }
    
    signOut() {
      const url = getSignOutPath();
      this.store.dispatch(setUserInfo(null));
      this.reload( url );
    }
    
    startMeeting() {
        const url = getStartMeetingPath();
        if( isInStandaloneMode() ) {
          this.openedBrowsingContext = this.openWindow(url);
      } else {
          this.openedBrowsingContext =  this.openTab(url);
      }
    }
    
    _startMeeting() {
      window.location.href = url;
      const url = getStartMeetingPath();
      let features = isInStandaloneMode() ? this.windowFeatures : "";
      if (this.openedBrowsingContext == null || this.openedBrowsingContext.closed) {
          this.openedBrowsingContext = window.open(url, this.windowName, features);
      } else {
          this.openedBrowsingContext.focus();
      }

      if (this.openedBrowsingContext != null) {
          this.store && this.store.dispatch(setIsDuringMeeting(true));
      }
      if(isInStandaloneMode()) {
        // pwa installed. open a window
      } else {
        // open a tab
      }
    }
    
    joinMeeting(meetingId, params = {}) {
        const url = getJoinMeetingPath(meetingId, params);
        if( isInStandaloneMode() ) {
          this.openedBrowsingContext = this.openWindow(url);
        } else {
          this.openedBrowsingContext = this.openTab(url);
        }
    }
  
    _joinMeeting(meetingId, params = {}) {
      const url = getJoinMeetingPath(meetingId, params);
        let features = isInStandaloneMode() ? this.windowFeatures : "";
        if (
            this.openedBrowsingContext == null ||
            this.openedBrowsingContext.closed ||
            !/wc\/\d{8,11}\/(join|start)/.test(this.openedBrowsingContext.location.pathname)
        ) {
            this.openedBrowsingContext = window.open(url, this.windowName, features);
            this.startListen();
        } else {
            this.openedBrowsingContext.focus();
        }

        if (this.openedBrowsingContext != null) {
            this.store && this.store.dispatch(setIsDuringMeeting(true));
        }
    }
    
    meetingEnded() {
        this.store && this.store.dispatch(setIsDuringMeeting(false));
        this.openedBrowsingContext = null;
    }

    handleMessage(event) {
        console.log("from opened window message: ", event.data, event.origin);
    }

    startListen() {
        window.addEventListener('message', this.handleMessage);
    }

    endListen() {

    }

}